import React, { Fragment } from "react";
import foto from "../../img/foto.jpg";
import SocialMedia from "./SocialMedia";

const Banner = () => {
  return (
    <Fragment>
      <div className="banner">
        <img src={foto} alt="" />
        <h2 id="Text">
          <span>Web</span>
          <span>Backend</span>
          <span>Frontend</span>
          <span>Full Stack</span>
          &nbsp; Developer
        </h2>
        <a
          href="https://drive.google.com/file/d/18MFJEVhS_XS1gTrQceWcxeTZ-HKd-C3P/view?usp=sharing"
          className="cv"
          target="_blank"
        >
          <span className="cv-btn">CV</span>
          <span className="cv-btn">CV</span>
        </a>
        <a
          href="https://drive.google.com/file/d/1lRwL7G8S0vS9fmkI8BUicwW5O10fi_MW/view?usp=sharing"
          className="portofolio"
          target="_blank"
        >
          <span className="portofolio-btn">Portofolio</span>
          <span className="portofolio-btn">Portofolio</span>
        </a>
      </div>
      <SocialMedia />
    </Fragment>
  );
};

export default Banner;
