import React, { Component } from "react";
import $ from "jquery";

class SocialMedia extends Component {
  constructor(props) {
    super(props);
    this.actionToggle = this.actionToggle.bind(this);
  }

  componentDidMount() {
    window.addEventListener("scroll", function () {
      var navbar = document.querySelector("nav");
      if ($(this).scrollTop() > 0) {
        navbar.classList.add("sticky");
      } else {
        navbar.classList.remove("sticky");
      }
    });
    window.addEventListener("resize", function () {
      if (window.innerWidth > 900)
        document.querySelector("nav").classList.remove("active");
    });
    var text = document.getElementById("Text");
    var word = text.getElementsByTagName("span");
    var i = 0;
    function rotator() {
      word[i].style.display = "none";
      i = (i + 1) % word.length;
      word[i].style.display = "initial";
    }
    setInterval(rotator, 2000);
    $(".bars").click(function () {
      $(".bars").css("transfrom", "translateX(-300px)");
      document.querySelector("nav").classList.toggle("active");
    });
    $("#about_nav").click(function () {
      $("html, body").animate(
        {
          scrollTop: $("#about").offset().top - 100,
        },
        1000
      );
    });
    $("#education_nav").click(function () {
      $("html, body").animate(
        {
          scrollTop: $("#education").offset().top - 100,
        },
        1000
      );
    });
    $("#skills_nav").click(function () {
      $("html, body").animate(
        {
          scrollTop: $("#skills").offset().top - 100,
        },
        1000
      );
    });
    $("#project_nav").click(function () {
      $("html, body").animate(
        {
          scrollTop: $("#project").offset().top - 100,
        },
        1000
      );
    });
    $("#contact_nav").click(function () {
      $("html, body").animate(
        {
          scrollTop: $("#contact").offset().top - 100,
        },
        1000
      );
    });
    $("#portofolio_nav").click(function () {
      $("html, body").animate(
        {
          scrollTop: $(".banner").offset().top - 200,
        },
        1000
      );
    });
  }

  actionToggle() {
    var action = document.querySelector(".action");
    action.classList.toggle("active");
  }

  render() {
    return (
      <div class="action" onClick={this.actionToggle}>
        <span class="tombol">
          <img src="https://img.icons8.com/android/24/000000/plus.png" />
        </span>
        <ul>
          <li>
            <a href="https://www.facebook.com/heris783/" target="_blank">
              <img src="https://img.icons8.com/fluent/48/000000/facebook-new.png" />
              Facebook
            </a>
          </li>
          <li>
            <a href="https://twitter.com/arisman_heri" target="_blank">
              <img src="https://img.icons8.com/fluent/48/000000/twitter.png" />
              Twitter
            </a>
          </li>
          <li>
            <a href="https://www.instagram.com/heri_san19" target="_blank">
              <img src="https://img.icons8.com/fluent/48/000000/instagram-new.png" />
              Instagram
            </a>
          </li>
          <li>
            <a href="https://github.com/herisusantoarismann" target="_blank">
              <img src="https://img.icons8.com/fluent/48/000000/github.png" />
              Github
            </a>
          </li>
        </ul>
      </div>
    );
  }
}

export default SocialMedia;
