import React, { Fragment } from "react";
import Foto from "../../img/project1.png";

const Project = () => {
  return (
    <Fragment>
      <div className="container">
        <div className="project">
          <div className="card">
            <div className="imgBx">
              <img src={Foto} alt="" />
            </div>
            <div className="contentBx">
              <h2>Kuliner</h2>
              <a href="#">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                To Github
              </a>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default Project;
